import axios from 'axios'
import Toast from '@/toolkits/Toast'

let envConfig = {
  baseUrl: process.env.NODE_ENV === 'production' ? '' : '/dev'
}

// åˆ›å»º json å®žä¾‹
let axiosJSON = axios.create({
  responseType: 'json',
  timeout: 60000, // 10s è¶…æ—¶
  baseURL: envConfig.baseUrl,
  withCredentialsq: true
})

// å¤„ç†è¿”å›žé”™è¯¯
axiosJSON.interceptors.response.use(response => {
  if (response && response.data && response.data.success) return response.data.data || response.data.success
  else {
    Toast.show('è¯·æ±‚å¤±è´¥' + (response.data.errorMsg ? `ï¼ŒåŽŸå› æ˜¯ï¼š${response.data.errorMsg}` : ''))
    return false
  }
})

let connect = async function (params) {
  let result = await axiosJSON(params).catch(() => false)
  // if (!result) Toast.show('è¯·æ±‚å¤±è´¥')
  return result
}

export default connect



