<template>
  <div class="m-v-40">
    <page-header zh="å½’æ¡£" en="Archives"></page-header>
    <archive-month v-for="(month, key) of articleList" :key="key" :monthData="month"></archive-month>
  </div>
</template>

<script>
  import ArchiveMonth from './ArchiveMonth'
  import PageHeader from '../common/PageHeader'

  export default {
    data () {
      return {
        articleData: []
      }
    },
    computed: {
      articleList () {
        let articleData = this.articleData
        let list = {}

        if (Array.isArray(articleData)) {
          // æŠŠæ•°æ®ä»Žå‡åºæ”¹ä¸ºé™åº
          articleData = articleData.reverse()
          // æ ¹æ®æœˆä»½åˆ†ç»„
          for (let item of articleData) {
            let id = this.getYearMonth(item.date)
            item.id = id
            if (!list[id]) list[id] = []
            list[id].push(item)
          }
        }
        return list
      }
    },
    methods: {
      getYearMonth (date) {
        if (typeof date === 'string' && !!date.match(/[0-9]{4}-[0-9]{2}-[0-9]{2}/)) return date.slice(0, 7)
        else return 'NODATE'
      }
    },
    mounted () {
      this.articleData = [
        {date: '2017-01-01', title: 'è®¨è®º base64 å›¾ç‰‡åœ¨å‰ç«¯çš„è¡¨çŽ°', category: 'HTML', time: '19:28', tags: ['base64', 'å‰ç«¯å·¥ç¨‹', 'å›¾ç‰‡ä¼˜åŒ–']},
        {date: '2017-01-02', title: 'è®¨è®º svg åœ¨å‰ç«¯çš„è¡¨çŽ°', category: 'HTML', time: '19:28', tags: ['base64', 'å‰ç«¯å·¥ç¨‹', 'å›¾ç‰‡ä¼˜åŒ–']},
        {date: '2017-02-02', title: 'åˆ†æž vue çš„å“åº”å¼ç³»ç»Ÿ', category: 'javascript', time: '19:28', tags: ['vue', 'å“åº”å¼', 'åº•å±‚å®žçŽ°']}
      ]
    },
    components: {
      ArchiveMonth,
      PageHeader
    }
  }
</script>

<style lang="scss" scoped>

</style>



